import Navbar from "../components/Navbar";
import { useNavigation } from "@react-navigation/native";
import React, { useEffect, useState } from "react"; // userState es un hook que permite manejar el estado en componentes
import { Image, Text, useWindowDimensions, View } from "react-native";
import { useUsuario } from "../contexts/UsuarioContext";
import { isPermiso } from "../services/RolPermisosAndRol";
import { usePermisosUsuario as usePermisosUsuarioNavbar } from "../contexts/PermisosNavbarContext";


export function HomeScreen() {
  const { width } = useWindowDimensions(); // Obtiene el ancho de la ventana
  const isMobile = width < 930;
  const navigation = useNavigation();
  const { setUsuario, usuario } = useUsuario();
  const { permisosUsuarioNavbar, setPermisosUsuarioNavbar } = usePermisosUsuarioNavbar();
  
  useEffect(() =>{

    const cargarPermisosDeUsuario = async () =>{
      if (usuario?.token) {
        
        const resultProductoView = await isPermiso(
          usuario.token,
          "9",
          usuario.id_usuario
        );
        const resultProveedorView = await isPermiso(
          usuario.token,
          "38",
          usuario.id_usuario
        );
        const resultEntradaView = await isPermiso(
          usuario.token,
          "13",
          usuario.id_usuario
        );
        const resultMovimientoLocalView = await isPermiso(
          usuario.token,
          "30",
          usuario.id_usuario
        );
        const resultMovimientoGeneralView = await isPermiso(
          usuario.token,
          "31",
          usuario.id_usuario
        );
        const resultAgregarUsuario = await isPermiso(
          usuario.token,
          "1",
          usuario.id_usuario
        );
        const resultModificarUsuario = await isPermiso(
          usuario.token,
          "2",
          usuario.id_usuario
        );
        const resultEliminarUsuario = await isPermiso(
          usuario.token,
          "3",
          usuario.id_usuario
        );
        const resultAccionesView = await isPermiso(
          usuario.token,
          "15",
          usuario.id_usuario
        );
        const resultAgregarTipoServicioView = await isPermiso(
          usuario.token,
          "20",
          usuario.id_usuario
        );
        const resultModificarTipoServicioView = await isPermiso(
          usuario.token,
          "21",
          usuario.id_usuario
        );
        const resultEliminarTipoServicioView = await isPermiso(
          usuario.token,
          "22",
          usuario.id_usuario
        );
        const resultEliminarClienteView = await isPermiso(
          usuario.token,
          "18",
          usuario.id_usuario
        );
        const resultModificarClienteView = await isPermiso(
          usuario.token,
          "17",
          usuario.id_usuario
        );
        const resultAgregarClienteView = await isPermiso(
          usuario.token,
          "16",
          usuario.id_usuario
        );
        const resultVerClienteView = await isPermiso(
          usuario.token,
          "19",
          usuario.id_usuario
        );
        const resultExpedirGarantiaView = await isPermiso(
          usuario.token,
          "32",
          usuario.id_usuario
        );
        const resultModificarGarantiaView = await isPermiso(
          usuario.token,
          "33",
          usuario.id_usuario
        );
        const resultVerGarantiaView = await isPermiso(
          usuario.token,
          "34",
          usuario.id_usuario
        );
        
        setPermisosUsuarioNavbar({
          resultAccionesView: resultAccionesView,
          resultAgregarUsuario: resultAgregarUsuario,
          resultEliminarUsuario: resultEliminarUsuario,
          resultEntradaView: resultEntradaView,
          resultModificarUsuario: resultModificarUsuario,
          resultMovimientoGeneralView: resultMovimientoGeneralView,
          resultMovimientoLocalView: resultMovimientoLocalView,
          resultProductoView: resultProductoView,
          resultProveedorView: resultProveedorView,
          resultTipoServicio: (resultAgregarTipoServicioView || resultModificarTipoServicioView || resultEliminarTipoServicioView),
          resultClienteView: (resultVerClienteView || resultAgregarClienteView || resultModificarClienteView || resultEliminarClienteView),
          resultGarantiaView: (resultVerGarantiaView || resultExpedirGarantiaView || resultModificarGarantiaView)
        });

      }
    }

    cargarPermisosDeUsuario();

  },[])

  if (isMobile) {
    return (
      <View style={{ flex: 1 }}>
        <Navbar />
        <View
          style={{
            flex: 1,
            justifyContent: "space-around",
            alignItems: "center",
            flexDirection: "column"
          }}
        >
          <Image
            source={require("../images/solutel.jpg")}
            style={{ width: "60%", height: "55%" }}
          />
          <Image
            source={require("../images/solutel1.jpg")}
            style={{ width: "90%", height: "30%" }}
          />
        </View>
      </View>
    );
  }else{
    return (
      <View style={{ flex: 1 }}>
        <Navbar />
        <View
          style={{
            flex: 1,
            justifyContent: "space-around",
            alignItems: "center",
            flexDirection: "row"
          }}
        >
          <Image
            source={require("../images/solutel.jpg")}
            style={{ width: "25%", height: "60%", marginTop: "5%" }}
          />
          <Image
            source={require("../images/solutel1.jpg")}
            style={{ width: "50%", height: "40%" }}
          />
        </View>
      </View>
    );
  }
}
