// navigationRef.ts
import { createRef } from "react";
import { NavigationContainerRef } from "@react-navigation/native";
import { RootStackParamList } from "../App";

// Define la referencia de navegacin global con el tipo correcto
export const navigationRef = createRef<NavigationContainerRef<RootStackParamList>>();

// Funcin de navegacin global
export function navigate<RouteName extends keyof RootStackParamList>(
  name: RouteName,
  params?: RootStackParamList[RouteName]
) {
  // Asegrate de que los parmetros sean correctamente tipados y pasados
  navigationRef.current?.navigate(name, params as any);
}
