import * as React from "react";
import { DataTable } from "react-native-paper";
import {
  View,
  StyleSheet,
  Text,
  TouchableOpacity,
  useWindowDimensions,
  Button,
  Animated,
  Image,
  Modal,
  ScrollView,
} from "react-native";
import { Colors } from "../styles/Colors";
import { useNavigation } from "@react-navigation/native";

export interface TiendaShowModal {
  id_tienda: string;
  nombre: string;
  cantidad: string;
}

interface Props {
  items: TiendaShowModal[];
  columns: string[];
}

export const MyDateTableModalShowDatesTienda: React.FC<Props> = ({
  items,
  columns,
}) => {
  const navigation = useNavigation();
  const [scale] = React.useState(new Animated.Value(1));

  const [page, setPage] = React.useState<number>(0);
  const [itemsPerPage, setItemsPerPage] = React.useState<number>(3);
  const [displayedItems, setDisplayedItems] = React.useState<TiendaShowModal[]>(
    []
  ); // Estado para los elementos mostrados

  React.useEffect(() => {
    // Actualiza los elementos mostrados al cambiar de pgina
    const from = page * itemsPerPage;
    const to = Math.min((page + 1) * itemsPerPage, items.length);

    // Limpia los elementos actuales antes de actualizar
    setDisplayedItems([]); // Limpia los elementos de la tabla
    setTimeout(() => {
      setDisplayedItems(items.slice(from, to)); // Muestra los nuevos elementos despus de un breve intervalo
    }, 0); // Opcional: puedes ajustar este tiempo si quieres un pequeo retraso visual
  }, [page, items, itemsPerPage]);

  const handleChangePage = (newPage: number) => {
    if (newPage >= 0 && newPage * itemsPerPage < items.length) {
      setPage(newPage); // Cambia de pgina si es vlido
    }
  };

  // Funcin para mostrar las columnas de la tabla
  const showTableColumns = () => {
    return (
      <DataTable.Header style={{ justifyContent: "space-between" }}>
        {columns.map((column, index) => (
          <DataTable.Title
            key={index}
            style={[
              styles.headerColumn,
              {
                flex: 1,
                justifyContent: "center",
                flexDirection: "row",
                alignItems: "center",
              },
            ]}
            numeric={index > 1}
          >
            <Text style={styles.headerTextDesktop}>{column}</Text>
          </DataTable.Title>
        ))}
      </DataTable.Header>
    );
  };
  const onPressIn = () => {
    Animated.spring(scale, {
      toValue: 1.5,
      useNativeDriver: true,
    }).start();
  };

  const onPressOut = () => {
    Animated.spring(scale, {
      toValue: 1,
      useNativeDriver: true,
    }).start();
  };

  const [isModalVisible, setModalVisible] = React.useState(false);

  // Funcin para abrir/cerrar el modal
  const toggleModal = () => {
    setModalVisible(!isModalVisible);
  };

  return (
    <View style={styles.containerDesktop}>
      <ScrollView horizontal={true} showsHorizontalScrollIndicator={false}>
        <DataTable style={{ width: "150%" }}>
          {showTableColumns()}

          {displayedItems.filter((item)=>parseInt(item.cantidad) !== 0).map((item) => (
            <DataTable.Row
              key={item.id_tienda}
              onPress={() => {}}
            >
              <DataTable.Cell style={styles.handerRow}>
                <Text
                  style={{ flexWrap: "wrap", width: "100%", textAlign: "left" }}
                  numberOfLines={2} // Limita a 2 lneas
                >
                  {item.nombre}
                </Text>
              </DataTable.Cell>
              <DataTable.Cell numeric style={styles.handerRow}>
                {item.cantidad}
              </DataTable.Cell>
            </DataTable.Row>
          ))}

          <View style={styles.paginationContainer}>
            <TouchableOpacity
              style={[
                styles.paginationButtonMovil,
                page === 0 && styles.disabledButton,
              ]}
              onPress={() => handleChangePage(page - 1)}
              disabled={page === 0} // Desactiva el botn si es la primera pgina
            >
              <Text style={styles.paginationLabel}>Anterior</Text>
            </TouchableOpacity>

            <Text style={styles.paginationLabel}>
              {`${page * itemsPerPage + 1}-${Math.min(
                (page + 1) * itemsPerPage,
                items.length
              )} of ${items.length}`}
            </Text>

            <TouchableOpacity
              style={[
                styles.paginationButtonMovil,
                (page + 1) * itemsPerPage >= items.length &&
                  styles.disabledButton,
              ]}
              onPress={() => handleChangePage(page + 1)}
              disabled={(page + 1) * itemsPerPage >= items.length} // Desactiva si es la ltima pgina
            >
              <Text style={styles.paginationLabel}>Prxima</Text>
            </TouchableOpacity>
          </View>
        </DataTable>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  containerDesktop: {
    flex: 1,
    padding: 10,
    marginTop: "2%",
  },
  containerMovil: {
    flex: 1,
    marginTop: "2%",
    marginBottom: "20%",
  },
  headerTextDesktop: {
    fontSize: 18,
    fontWeight: "bold",
  },
  headerTextMovil: {
    fontSize: 12,
    fontWeight: "bold",
  },
  headerColumn: {
    justifyContent: "center",
  },
  handerRow: {
    fontSize: 14,
    justifyContent: "center",
    fontWeight: "bold",
    width: "100%",
  },
  paginationContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    paddingVertical: 10,
  },
  paginationButtonDesktop: {
    backgroundColor: Colors.azul_Suave,
    borderRadius: 20,
    width: "15%",
    height: 40,
    alignItems: "center",
    justifyContent: "center",
    shadowColor: Colors.azul_Suave,
    shadowOffset: { width: 0, height: 0 },
    shadowOpacity: 0.6,
    shadowRadius: 14,
    elevation: 3,
  },
  paginationButtonMovil: {
    backgroundColor: Colors.azul_Suave,
    borderRadius: 20,
    width: "30%",
    height: 40,
    marginHorizontal: "3%",
    alignItems: "center",
    justifyContent: "center",
    shadowColor: Colors.azul_Suave,
    shadowOffset: { width: 0, height: 0 },
    shadowOpacity: 0.6,
    shadowRadius: 14,
    elevation: 3,
  },
  paginationLabel: {
    fontSize: 14,
    color: Colors.negro,
  },
  disabledButton: {
    backgroundColor: Colors.blanco_Suave,
  },
});
